.inicial-container{
    height: 50vw;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: rgb(21, 21, 21);
}

.aboutme{
    font-size: 3.5vw;
    text-align: center;
    color: #da274b; 
    




    font-family: 'Parisienne', cursive;




    


}

.descricao{
    text-align: justify;
}

.boasvindas{
    color: white;
    text-transform: uppercase;

    font-family: 'Raleway', sans-serif;
    font-size: 4vw;

}

.botao{
    padding: 2%;
    text-decoration: none;
    font-family: 'Raleway', sans-serif;
    color: white;
    text-transform: uppercase;
    font-size: 1.5vw;



}

.setinha{
    width: 5vw;
    height: 5vw;
}

.setinha:hover{
    background: #d87085;
    border-radius: 50%;
    
}