.contato-container{
    height: 50vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: #e2e2e2;



}

.contato{
    font-size: 2.5vw;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    width: 100%;
    color: #da274b;

}

.icons-container{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    text-align: center;
    align-items: center;
    width: 100%;

    
    
    
}

.icons{
    width: 60%;
    height: 60%;
    padding: 2%;
    border-radius: 100%;

}

.social-medias{
    font-size: 1.5vw;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    width: 100%;
    
}

.social{
    padding: 0% 10% 0% 10%;
    width: 30%;
    height: 30%;

}


.email{
    text-align: justify;
    color: black;
    font-size: 1.5vw;
    
}