.sobre-mim-container{
    height: 50vw;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #e2e2e2;
    width: 100%;


}

.foto-container{
    display: flex;
    float: left;
    width: 25vw;
   
}

.foto-perfil{
    border-radius: 100%;
    width: 100%;
    height: 100%;
}



.titulo{
    font-size: 2.5vw;
    text-align: left;
    text-transform: uppercase;
    color: #da274b; 
    padding-bottom: 20px;
    font-family: 'Raleway', sans-serif;


    
}

.descricao{
    text-align: justify;
    color: rgb(54, 51, 51);
    font-family: 'Roboto', sans-serif;
    font-size: 1.5vw;

    
}


.apresentacao{
    width: 60%;
    text-align: center;
    float: right;
    height: 60%;
    
    

}

